import React, { Component } from 'react';
import Product from './Product';
import Filters from './Filters';
import celulares from '../../json/celulares';
import NotFound from '../assets/NotFound';

class App extends Component{

  state = {
    allProducts: celulares,
    productVisible: true,
    activeFilter: []
  }

  componentDidMount(){
    this.setState({
      activeFilter: this.state.allProducts
    })
  }

  /* Filtro de orden */

  orderProducts = () =>{
    this.setState({
      activeFilter: this.props.maya
    })
  }

  /* Filtro de casillas de verificación */

  onFilterChange = (x, y) =>{
    this.setState({
      activeFilter: x,
      productVisible: y
    })
  }

  render(){
    return(
      <div className="container">
        <aside className="edit-products">
          <Filters changeElements={this.onFilterChange} changeOrder={this.orderProducts} />
        </aside>
        <section className="products">
        { this.state.productVisible ? this.state.activeFilter.map( x =>{
          return (
            <Product key={x.id}
            imagen={x.imagen} modelo={x.modelo} stock={x.stock} precio={x.precio} />
            )
          })
          : <NotFound text="Ninguna casilla ha sido marcada..." />
        }
        </section>
      </div>
    )
  }
}

export default App;