import Icon from './Icon';
import './css/buscador.css';

function search(){
  const seeker = document.getElementById('seeker').value,
  products = document.getElementsByClassName('product');
  if (seeker === ""){
    for (let x=0; x<products.length; x++){
      products[x].style.display = "inline-table";
    }
  } else {
    for (let x=0; x<products.length; x++){
    const models = products[x].getElementsByTagName("h3")[0].textContent.toLocaleLowerCase();
      if (models.indexOf(seeker.toLocaleLowerCase()) !== -1){
        products[x].style.display = "inline-table";
      } else {
        products[x].style.display = "none";
      }
    }
  }
  
}

const Buscador = () =>{
    return(
      <form onSubmit={ e => {
        e.preventDefault();
        search();
        }
      }>
      <input type="text" id="seeker" placeholder="Busca algún producto..." />
      <button type="submit"><Icon iconName="fas fa-search" iconStyle={{padding: "0"}}/></button>
      </form>
    )
}

export default Buscador;